@import url(https://fonts.googleapis.com/css2?family=Revalia&display=swap);
body {
  margin: 0;
  padding:0;
  background-color: rgb(23, 23, 23);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.footer-container {
    position: absolute;
    bottom: 0;
    margin: 1.5em;
}

.contact {
    color: white;
    padding: 0.5em;
}
.slideContainer {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;

    background: linear-gradient(0deg, rgb(0, 0, 0) 20%, rgb(63, 6, 50) 75%);
}
.slideGrid {
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    width: 100vw;
    height: 100vh;
    display: grid;
    grid-template-rows: 1fr auto 1fr;
    justify-items: center;
}
.slide {
    grid-row: 2/3;
}
.slideNav {
    position: relative;
    text-align: center;
}

.text {
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    text-align: center;
    font-size: 250%;
    color: white;
}

.letter, .letterContainer {
    display: inline-block;
    padding: 0.1em;
}

.space {
    padding-right: 0.5em;
}

*,
*::before,
*::after {
  margin: 0;
}

.projects-page {
  font-family: "Quicksand", sans-serif;
  margin-top: 1em;
  display: grid;
  place-items: center;
  height: 100%;
  background: linear-gradient(0deg, rgb(0, 0, 0) 20%, rgb(63, 6, 50) 75%);
}

.project-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    max-width: 1200px;
    margin-block: 2rem;
    grid-gap: 2rem;
    gap: 2rem;
}

.card {
    display: flex;
    flex-direction: column;
    width: clamp(20rem, calc(20rem + 2vw), 22rem);
    overflow: hidden;
    border-radius: 1em;
    background: rgb(72, 72, 72);
    color: white;
}

.card__body {
    padding: 1rem;
    display: flex;
    flex-direction: column;
    grid-gap: .5rem;
    gap: .5rem;
}

a {
    color: white;
}

p, ul {
    font-size: 1.2em;
}

ul {
    list-style-type: circle;
}

li {
    margin: 10px 0;
}

.tag {
    align-self: flex-start;
    padding: .25em .75em;
    border-radius: 1em;
    font-size: .75rem;
}

.tag {
    font-size: 1em;
}

.tag-blue {
    background: linear-gradient(to bottom, #2F80ED, #56CCF2);
}

.tag-brown {
    background: linear-gradient(to bottom, #FFD194, #D1913C);
}

.tag-red {
    background: linear-gradient(to bottom, #ef473a, #cb2d3e);
}

.card__body h4 {
    font-size: 1.5rem;
    text-transform: capitalize;
}

.card__footer {
    padding: 1rem;
    margin-top: auto;
}

.date {
    color: rgb(255, 255, 255);
    font-size: 1.2em;
}
